@import url(https://fonts.googleapis.com/css2?family=Alfa+Slab+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Roboto:ital,wght@0,100;0,300;0,400;0,700;1,500;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Roboto:ital,wght@0,100;0,300;0,400;0,700;1,500;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background: #F6F6F6;
}

.title {
  font-family: 'Alfa Slab One', cursive;
  letter-spacing: 0.1em;
  color: rgba(234, 60, 46, 0.8);
}

.App {
  text-align: center;
  margin: auto;
  box-sizing: border-box;
}

.pokemon__container {
  width: 90%;
  margin: auto;
  height: 100vh;
}

.hero__image {
  margin-top: 0.5em;
}

form {
  margin-top: 2em;
}

.input_field {
  border: none;
  border-bottom: 1px solid rgb(143, 143, 143);
  width: 50%;
  padding: 0.5em;
  padding-top: 1.4em;
  padding-bottom: 0.4em;
  outline: none;
  background: white;
}

.search_btn {
  border: none;
  text-align: center;
  color: white;
  cursor: pointer;
  margin: 0;
  padding: 0.5em;
  -webkit-align-items: center;
          align-items: center;
  background: #feca1b;
}

.search_btn:hover {
  background: #c99f15;
}

.chips_container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 1em;
  margin-top: 0.5em;
}

.mini_chip {
  padding-bottom: 0.5em;
  margin: 0 0.5em;
}

@media(max-width:1024px) {
  .App {
    background: none;
  }

  .pokemon__container {
    width: 80%;
  }
}

@media(max-width:600px) {
  .pokemon__container {
    width: 70%;
  }

  .hero__image {
    width: 50%;
  }

}
.pokemon__card {
    width: 400px;
    margin: auto;
    box-shadow: 1px 2px 20px 0 rgba(0, 0, 0, 0.4);
    border-radius: 25px;
}

.empty {
    display: none;
}

h3,
h4 {
    font-family: 'Roboto', sans-serif;
}

h4 {
    font-weight: 900;
}

.title,
.type,
.candy {
    text-transform: capitalize;
}

.title h3 {
    margin-bottom: 0;
}

.pokemon__card:hover {
    box-shadow: 1px 2px 20px 0 rgba(0, 0, 0, 0.5);
}

.heart {
    color: tomato;
}

.heart_emoji {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.hp_info {
    padding-bottom: 0.3em;
}

.head__info {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin: 2em;
    padding-top: 2em;
    padding-bottom: 2em;
}

.body_info {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    background: rgba(211, 211, 211, 0.4);
    width: 400px;
    -webkit-align-items: center;
            align-items: center;
    margin: auto;
    border-radius: 10px;
    font-weight: 500;
    letter-spacing: 0.1em;
    color: black;
}

.body_info small {
    padding: 0.5em 2em;
}

.type_info {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    padding-bottom: 1.5em;
}

.image {
    width: 170px;
}

small {
    font-weight: 500;
}
body {
    box-sizing: border-box;
}

.empty {
    display: none;
}



.name_container {
    display: grid;
    grid-template-columns: repeat(6, 120px);
    grid-gap: 10px;
    gap: 10px;
    margin: 0 1em;
    -webkit-justify-content: center;
            justify-content: center;
}

.hover_class {
    text-transform: capitalize;
    background: white;
    padding: 0.7em;
    border-radius: 25px;
    box-shadow: 1px 2px 20px 0 rgba(0, 0, 0, 0.2);
    font-weight: 400;
    cursor: pointer;
}

.hover_class:hover {
    box-shadow: 1px 2px 20px 0 rgba(0, 0, 0, 0.4);
}


@media(max-width:800px) {
    .name_container {
        display: grid;
        grid-template-columns: repeat(3, 200px);
        grid-gap: 10px;
        gap: 10px;
        -webkit-justify-content: center;
                justify-content: center;
    }

}

@media(max-width:700px) {
    .name_container {
        display: grid;
        grid-template-columns: repeat(1, 200px);
        grid-gap: 10px;
        gap: 10px;
        -webkit-justify-content: center;
                justify-content: center;
    }
}
